/**
 * External Dependencies
 */
import { MediaDropZone } from '@prc/components';

/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import { Fragment, useState, useEffect } from '@wordpress/element';
import { useBlockProps, RichText } from '@wordpress/block-editor';
import { Button, TreeSelect } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { useEntityRecords } from '@wordpress/core-data';

/**
 * Internal Dependencies
 */
import Controls from './Controls';
import CollectionLinks from './CollectionLinks';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @param {Object}   props               Properties passed to the function.
 * @param {Object}   props.attributes    Available block attributes.
 * @param {Function} props.setAttributes Function that updates individual attributes.
 *
 * @return {WPElement} Element to render.
 */
export default function Edit({
	attributes,
	setAttributes,
	context,
	clientId,
	isSelected,
}) {
	const blockProps = useBlockProps();

	const { pdf } = attributes;
	const { id } = pdf || {
		id: false,
	};

	const { termIds = [] } = useSelect((select) => {
		const { getEditedPostAttribute } = select('core/editor');
		return {
			termIds: getEditedPostAttribute('collection'),
		};
	}, []);

	const { records, isResolving, hasResolved } = useEntityRecords(
		'taxonomy',
		'collection',
		{
			per_page: 50,
			context: 'view',
		},
	);

	return (
		<Fragment>
			<Controls {...{ attributes, setAttributes, context }} />
			<div {...blockProps}>
				<CollectionLinks
					records={records}
					termIds={termIds}
					isResolving={isResolving}
					hasResolved={hasResolved}
				/>
				<MediaDropZone
					attachmentId={id}
					onUpdate={(attachment) => {
						const newAttachment = {
							id: attachment.id,
							slug: attachment.slug,
							title: attachment.title,
							link: attachment.link,
							description: attachment.description,
							caption: attachment.caption,
							alt_text: attachment.alt_text,
							media_type: attachment.media_type,
							mime_type: attachment.mime_type,
							media_details: attachment.media_details,
							post: attachment.post,
							source_url: attachment.source_url,
						};
						setAttributes({ pdf: newAttachment });
					}}
					onClear={() => {
						console.warn(
							'Media DropZone Attachment, use onClear prop when using <MediaDropZone/>: ',
						);
						setAttributes({ pdf: null });
					}}
					mediaType={['application/pdf']}
					label="Upload PDF"
					singularLabel="PDF"
				>
					<Button variant="secondary" label="Update PDF" icon="pdf">
						{`Update ${pdf ? `${pdf.title}.pdf` : 'PDF'}`}
					</Button>
				</MediaDropZone>
			</div>
		</Fragment>
	);
}
