/**
 * External Dependencies
 */
import { wpRestApiTermsToTree } from '@prc/functions';
import md5 from 'md5';
import styled from '@emotion/styled';

/**
 * WordPress Dependencies
 */
import { __ } from '@wordpress/i18n';
import {
	memo,
	useMemo,
	useState,
	useEffect,
	Fragment,
} from '@wordpress/element';
import { RichText } from '@wordpress/block-editor';
import { Spinner } from '@wordpress/components';

const Collections = styled('div')`
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	flex-direction: row;
	padding-top: 1em;
	padding-bottom: 1em;
	margin-bottom: 1em;
	border-top: 1px solid rgba(34, 36, 38, 0.15);
	border-bottom: 1px solid rgba(34, 36, 38, 0.15);
`;

const Children = styled('div')`
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	flex-direction: row;
`;

const Collection = styled('div')`
	font-weight: ${(props) => (props.isActive ? 'bold' : 'inherit')};
`;

const ParentCollection = styled('div')`
	font-weight: bold;
`;

export default function CollectionLinks({
	records,
	isResolving,
	hasResolved,
	termIds = [],
}) {
	const [tree, setTree] = useState([
		{
			id: 0,
			name: 'Loading Collection...',
			children: [],
		},
	]);

	useEffect(() => {
		if (!isResolving && hasResolved) {
			console.log("Terms: ", records);
			setTree(wpRestApiTermsToTree(records, termIds));
		}
	}, [isResolving, hasResolved, records, termIds]);

	return (
		<Collections>
			{isResolving && <Spinner />}
			{tree.map((term) => (
				<Collection key={term.id ? term.id : md5(term.name)}>
					<ParentCollection>{term.name}</ParentCollection>
					{term.children && (
						<Children>
							{term.children.map((child) => {
								const isActive = termIds.includes(child.id);
								return (
									<Collection
										key={child.id ? child.id : md5(child.name)}
										isActive={isActive}
									>
										{child.name}
									</Collection>
								);
							})}
						</Children>
					)}
				</Collection>
			))}
		</Collections>
	);
}
